<template>
  
   <div  >
     <mu-appbar :title="pictureObject.title">
       <mu-icon-button icon="arrow_back" slot="left" @click="goback()" />

    </mu-appbar>
  <!--   <swiper :options="swiperOption" ref="mySwiper">
    <swiper-slide v-for="slide,index in pictureObject.images" :key="index">
      <img v-lazy="slide" style="width:100%;padding:5px;" >
    </swiper-slide>
       <div class="swiper-pagination"  slot="pagination"></div>
    <div class="swiper-button-prev" slot="button-prev"></div>
    <div class="swiper-button-next" slot="button-next"></div>
    </swiper> -->
     <div class="fixed-container">
    <div v-for="slide,index in pictureObject.images" :key="index">
     <img v-lazy="slide" lazy="loading">
   </div>
 </div>
    <mu-dialog  :open="dialog" title="广告">
