      <mu-flat-button  label="关闭" slot="actions" primary @click="()=>{dialog=false}"/>
      </mu-dialog>
    </div>
  </template>
  <script >
   export default {
    name:'pictureComponet',

    mounted(){
             
    },

    data(){
     return {
       swiperOption: {
        autoplay: 1000,
        setWrapperSize :true,
        centeredSlides:true,

        
          paginationClickable :true,
          prevButton:'.swiper-button-prev',
          nextButton:'.swiper-button-next',
      },
      dialog:false,

    }
  },
  computed:{
   pictureObject() {
    return this.$store.state.pvo
   },
   swiper(){
    return this.$refs.mySwiper.swiper
   }
   
},

methods: {


  goback()
  {
    this.$router.go(-1)
  }


} 
}
</script>
<style scoped>

 .flex{
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
}

.desc-container{
  height:100%;
  overflow-y: scroll;
}
img {
  width: 100%;
}
.fixed {
  position:fixed;
  top: 0;
}

</style>


